<template>
	<!-- 小程序二级详情页面 -->
	<div class="WxStage" id="WxStage">
		<div class="bgcontainer" v-for="(item,i) in listsData" :key="item.id">
			<el-container direction="vertical" class="content">
				<el-row :gutter="0" type="flex" style="" justify="space-between">
					<el-col :xs="24" :sm="10" :md="10" :lg="10" :xl="10">
						<div class="grid-content bg-purple-light top">
							<h1 class="title">{{item.logoTilte}}</h1>
							<p class="text">{{item.content}}</p>
							<a href="" class="btn">立即咨询</a>
						</div>
						<!-- 主要功能手机端显示 -->
						<el-col :xs="24" class="layerParent hidden-sm-and-up" style="background: pink;">
							<div class="grid-content bg-purple layer">
								<h1>主要功能</h1>
								<ul>
									<li v-for="icons in item.logoImages" :key="icons.id">
										<img :src="icons.image" alt="">
										<span>{{icons.tilte}}</span>
									</li>
								</ul>
							</div>
						</el-col>
					</el-col>
					<!-- 手机端隐藏 -->
					<el-col :xs="24" :sm="12" :md="12" :lg="14" :xl="14" class="layerParent hidden-xs-only">
						<img :src='item.logoImage' alt="">
						<div class="grid-content bg-purple layer">
							<h1>主要功能</h1>
							<ul>
								<li v-for="icons in item.logoImages" :key="icons.id">
									<img :src="icons.image" alt="">
									<span>{{icons.tilte}}</span>
								</li>
							</ul>
						</div>
					</el-col>
				</el-row>
			</el-container>
		</div>
	</div>
</template>

<script>
	import 'element-ui/lib/theme-chalk/display.css';
	export default ({
		data() {
			return {
				listsData: null,
			}
		},
		methods: {
			postData() {
				this.$http.post(process.env.VUE_APP_URL + '/logo/logoList', {}, {
					emulateJSON: true
				}).then(function(result) {
					this.listsData = result.body
					this.$options.methods.setLocalStorage("listsData", JSON.stringify(this.listsData))
				}, function(result) {
					console.log("没响应")
				})
			},
			setCookie(cname, cvalue, exdays) {
				let d = new Date();
				d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
				let expires = "expires=" + d.toUTCString();
				document.cookie = cname + "=" + cvalue + "; " + expires;
			},
			getCookie(cname) {
				let name = cname + "=";
				let ca = document.cookie.split(';');
				for (var i = 0; i < ca.length; i++) {
					let c = ca[i].trim();
					if (c.indexOf(name) == 0) {
						return c.substring(name.length, c.length);
					}
				}
				return "";
			},
			setLocalStorage(key, value) {
				window.localStorage.setItem(key, value);
			},
			getLocalStorage(key) {
				let temp = window.localStorage.getItem(key);
				if (temp) {
					return temp
				} else {
					return false
				}
			},
		},
		beforeMount: function() {
			this.postData()

		},
		mounted: function() {
			var a = this.getLocalStorage("listsData")
		}
	})
</script>
